.Cart {
    padding: 0 20px;
    padding-bottom: 200px;
}

.Cart .cart-items-count {
    font-size: 36px;
    margin-top: 20px;
    color: var(--primary-color-l-2);
    margin-bottom: 20px;
}
.Cart .cart-items-count span {
    color: var(--primary-color);
    font-weight: 600;
}

.Cart .divider {
    outline: 0;
    border: 0;
    border-top: 1px solid var(--secondary-color-d-3);
    width: calc(100% + 40px);
    margin-left: -20px;
}

.Cart .empty-cart-cta {
    width: auto;
    height: 64px;
    background-color: var(--secondary-color-d-3);
    border: 0;
    outline: 0;
    border-radius: 24px;
    display: inline-flex;
    margin-top: 24px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0 20px;
    transition: all 50ms ease-out;
    font-size: 24px;
    color: white;
    font-weight: normal;
    text-decoration: none;
}
.Cart .empty-cart-cta:hover {
    background-color: var(--secondary-color-d-2);
}
.Cart .empty-cart-cta:active {
    background-color: var(--secondary-color-d-4);
}

.Cart .cart-total, .Cart .cart-tax, .Cart .cart-grand-total {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    color: var(--primary-color);
    font-weight: normal;
    margin-top: 8px;
}
.Cart .cart-total {
    margin-top: 20px;
}
.Cart .cart-grand-total {
    font-weight: 600;
}

.Cart .checkout-btn-ctr {
    width: 100%;
    max-width: var(--viewport-width);
    position: fixed;
    top: auto;
    bottom: 88px;
    right: auto;
    left: auto;
    display: flex;
    justify-content: flex-end;
    padding-right: 40px;
}


.Cart .checkout-btn {
    width: auto;
    height: 64px;
    background-color: var(--secondary-color-d-3);
    border: 0;
    outline: 0;
    border-bottom-right-radius: 24px;
    border-top-left-radius: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0 20px;
    transition: all 50ms ease-out;
    font-size: 24px;
    color: white;
    font-weight: normal;
    text-decoration: none;
}
.Cart .checkout-btn:hover {
    background-color: var(--secondary-color-d-2);
}
.Cart .checkout-btn:active {
    background-color: var(--secondary-color-d-4);
}
.Cart .checkout-btn.disabled {
    background-color: var(--primary-color-l-4);
}
