.Checkout {
    padding: 0 20px;
    /* padding-bottom: 132px; */
    padding-bottom: 24px;
}


.Checkout .empty-cart-cta {
    width: auto;
    height: 64px;
    background-color: var(--secondary-color-d-3);
    border: 0;
    outline: 0;
    border-radius: 24px;
    display: inline-flex;
    margin-top: 24px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0 20px;
    transition: all 50ms ease-out;
    font-size: 24px;
    color: white;
    font-weight: normal;
    text-decoration: none;
}
.Checkout .empty-cart-cta:hover {
    background-color: var(--secondary-color-d-2);
}
.Checkout .empty-cart-cta:active {
    background-color: var(--secondary-color-d-4);
}

.Checkout .cart-items-count {
    font-size: 36px;
    margin-top: 20px;
    color: var(--primary-color-l-2);
    margin-bottom: 20px;
}
.Checkout .cart-items-count span {
    color: var(--primary-color);
    font-weight: 600;
}

.Checkout .checkout-page-title {
    font-size: 24px;
    font-weight: normal;
    color: var(--primary-color-l-2);
    margin-top: 20px;
}

.Checkout .checkout-stats {
    font-size: 36px;
    font-weight: 600;
    color: var(--primary-color);
}

.Checkout .divider {
    outline: 0;
    border: 0;
    border-top: 1px solid var(--secondary-color-d-3);
    width: calc(100% + 40px);
    margin-left: -20px;
    margin-top: 20px;
}


.Checkout .form-group {
    margin-top: 20px;
}
.Checkout .form-group label {
    font-size: 16px;
    font-weight: normal;
    /* color: var(--primary-color-l-2); */
    color: var(--primary-color);
    line-height: 1.3;
}
.Checkout .form-group input,
.Checkout .form-group select,
.Checkout .form-group textarea {
    height: 64px;
    width: 100%;
    display: block;
    outline: 0;
    border: 1px solid var(--secondary-color-d-3);
    border-radius: 24px;
    padding: 0 20px;
    font-size: 24px;
    font-weight: normal;
    color: var(--primary-color);
    margin-top: 8px;
    background-color: white;
}
.Checkout .form-group input:focus,
.Checkout .form-group select:focus {
    border-width: 2px;
    padding-right: 19px;
    padding-left: 19px;
}
.Checkout .form-group textarea:focus {
    border-width: 2px;
    padding: 15px 19px;
}
.Checkout .form-group input,
.Checkout .form-group select {
    padding-bottom: 2px;
}
.Checkout .form-group select {
    -webkit-appearance: none;
    -moz-appearance: none;
    background-position-x: calc(100% - 20px);
}
.Checkout .form-group textarea {
    resize: vertical;
    height: auto;
    padding: 16px 20px;
}
.Checkout .form-group input::placeholder,
.Checkout .form-group select::placeholder,
.Checkout .form-group textarea::placeholder {
    color: var(--primary-color-l-2);
}
.Checkout .form-group input[type="submit"] {
    background-color: var(--secondary-color-d-3);
    color: white;
    cursor: pointer;
}
.Checkout .form-group input[type="submit"]:hover {
    background-color: var(--secondary-color-d-2);
}
.Checkout .form-group input[type="submit"]:active {
    background-color: var(--secondary-color-d-4);
}
.Checkout .form-group input[type="button"] {
    color: var(--secondary-color-d-3);
    cursor: pointer;
}
.Checkout .form-group input[type="button"].readonly {
    color: var(--primary-color-l-4);
    border-color: var(--primary-color-l-4);
}
.Checkout .form-group input[type="button"].readonly:active {
    border-width: 1px !important;
}
.Checkout .form-group input[type="button"]:focus {
    border-width: 1px !important;
}
.Checkout .form-group input[type="button"]:active {
    border-width: 2px !important;
    padding-right: 19px;
    padding-left: 19px;
}

.Checkout .cart-total, .Checkout .cart-tax, .Checkout .cart-grand-total {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    color: var(--primary-color);
    font-weight: normal;
    margin-top: 8px;
}
.Checkout .cart-total {
    margin-top: 20px;
}
.Checkout .cart-grand-total {
    font-weight: 600;
}

.Checkout .terms-conditions-ctr {
    display: block;
    margin-top: 24px;
    color: var(--primary-color);
    text-align: center;
}