.TopBar {
    padding-top: 20px;
    /* padding: 0 20px; */
    display: flex;
    justify-content: space-between;
}

.TopBar a, .TopBar a:focus,
.TopBar button, .TopBar button:focus {
    color: var(--primary-color-l-2);
    text-decoration: none;
    transition: color 100ms ease-out;
    background-color: transparent;
    outline: 0;
    border: 0;
    cursor: pointer;
}
.TopBar a:hover,
.TopBar button:hover {
    color: var(--primary-color);
}
.TopBar a span,
.TopBar button span {
    margin-right: 8px;
    font-size: 16px;
    display: inline-block;
    /* transform: translateY(-1px); */
}
.TopBar a *,
.TopBar button * {
    vertical-align: middle;
}