.CartItem {
    display: flex;   
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 20px 0;
}

.CartItem .img-ctr {
    width: 96px;
    height: 96px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 24px;
}

.CartItem .close-icon {
    width: 32px;
    height: 32px;
    background-color: white;
    border: 1px solid var(--secondary-color-d-3);
    border-radius: 10000px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.CartItem .details {
    width: calc(100% - 140px);
    padding: 0 12px;
    font-size: 20px;
    color: var(--primary-color);
}
.CartItem .details a {
    text-decoration: none !important;
    color: var(--primary-color);
}
.CartItem .details .title {
    font-weight: normal;
}
.CartItem .details .sub-total {
    font-weight: 600;
}
.CartItem .details .sub-total span {
    vertical-align: middle;
}
.CartItem .details .sub-total .sub-total-eqn {
    font-size: 16px;
    color: var(--primary-color-l-2);
    font-weight: normal;
    display: inline-block;
    padding-top: 2px;
}
.CartItem .details .max-qty {
    font-size: 16px;
    display: block;
    color: var(--primary-color-l-2);
    margin-top: 4px;
}

.CartItem .qty-input-ctr {
    width: 42px;
}
.CartItem .qty-input-ctr input, .CartItem .qty-input-ctr button {
    width: 42px;
    height: 42px;
    outline: 0;
    border: 1px solid var(--secondary-color-d-3);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: white;
    margin-bottom: -1px;
    color: var(--primary-color);
}
.CartItem .qty-input-ctr input:focus, .CartItem .qty-input-ctr button:focus {
    outline: 0;
}
.CartItem .qty-input-ctr input {
    font-size: 20px;
}
.CartItem .qty-input-ctr button:active, .CartItem .qty-input-ctr input:focus {
    border-width: 2px;
}
.CartItem .qty-input-ctr button.inc-btn {
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
    cursor: pointer;
}
.CartItem .qty-input-ctr button.dec-btn {
    border-bottom-left-radius: 24px;
    border-bottom-right-radius: 24px;
    cursor: pointer;
}