.HomeHeader {
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.HomeHeader .icons-ctr a {
    margin-left: 32px;
    transition: opacity 100ms ease-out;
}
.HomeHeader .icons-ctr a:hover {
    margin-left: 32px;
    opacity: 0.6;
}
.HomeHeader .icons-ctr a:first {
    margin-left: 16px;
}