.BottomNavbar {
    width: 100%;
    max-width: var(--viewport-width);
    height: 68px;
    position: fixed;
    bottom: 0;
    right: auto;
    left: auto;
    top: auto;
    transform: translateX(-20px);
    box-sizing: content-box;
    display: flex;
    border: 1px solid var(--secondary-color-d-3);
    border-top-right-radius: 24px;
    border-top-left-radius: 24px;
    background-color: white;
    justify-content: space-around;
    align-items: flex-end;
}

.BottomNavbar .nav-item-ctr {
    text-align: center;
    text-decoration: none;
    color: var(--primary-color-l-2);
    font-weight: normal;
    margin-bottom: 6px;
}
.BottomNavbar .nav-item-ctr:hover span.nav-link-text {
    color: var(--primary-color);
}
.BottomNavbar .nav-item-ctr span.nav-link-text {
    margin-top: 4px;
    transition: color 100ms ease-out;
}
.BottomNavbar .nav-item-ctr span.nav-link-text.active {
    color: var(--primary-color);
    font-weight: 600;
}
.BottomNavbar .prominent-item .prominent-icon-ctr {
    width: 72px;
    height: 72px;
    display: inline-block;
    border-radius: 10000px;
    border: 3px solid white;
    background-color: white;
    overflow: hidden;
}
.BottomNavbar .prominent-item .prominent-icon-inner-ctr {
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 10000px;
    padding-top: 2px;
    border: 2px solid var(--secondary-color-d-3);
}
.BottomNavbar .prominent-item .prominent-icon-ctr span {
    font-size: 20px;
    font-weight: bold;
    color: var(--secondary-color-d-3);
}
.BottomNavbar .prominent-item .prominent-icon-ctr img {
    margin-top: -4px;
}