.TermsConditions {
    padding: 0 20px;
    padding-bottom: 116px;
}

.TermsConditions .cart-items-count {
    font-size: 36px;
    margin-top: 20px;
    color: var(--primary-color-l-2);
    margin-bottom: 20px;
}
.TermsConditions .cart-items-count span {
    color: var(--primary-color);
    font-weight: 600;
}

.TermsConditions .term-ctr {
    display: flex;
    font-size: 20px;
    justify-content: stretch;
    text-align: left;
    line-height: 1.681;
    /* margin-top: 20px; */
    width: cal(100% + 40px);
    margin-left: -20px;
    padding: 20px;
    flex-wrap: nowrap;
}
.TermsConditions .term-ctr:nth-child(odd) {
    background-color: var(--secondary-color);
}
.TermsConditions .term-ctr .num {
    width: 3ch;
    text-align: center;
    font-size: 28px;
    font-weight: normal;
    color: var(--secondary-color-d-3);
    line-height: 1.1;
}
.TermsConditions .term-ctr .point {
    font-weight: normal;
    color: var(--primary-color);
    font-size: 16px;
    padding-left: 20px;
    /* padding-top: 8px; */
    width: calc(100% - 3ch);
}
