.OrderDetails {
    padding: 0 20px;
    padding-bottom: 24px;
}

.OrderDetails.with-fixed-bottom-navbar {
    padding-bottom: 116px;
}

.OrderDetails .empty-cart-cta {
    width: auto;
    height: 64px;
    background-color: var(--secondary-color-d-3);
    border: 0;
    outline: 0;
    border-radius: 24px;
    display: inline-flex;
    margin-top: 24px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0 20px;
    transition: all 50ms ease-out;
    font-size: 24px;
    color: white;
    font-weight: normal;
    text-decoration: none;
}
.OrderDetails .empty-cart-cta:hover {
    background-color: var(--secondary-color-d-2);
}
.OrderDetails .empty-cart-cta:active {
    background-color: var(--secondary-color-d-4);
}

.OrderDetails .success-text {
    font-size: 20px;
    font-weight: 600;
    color: var(--primary-color);
    text-align: center;
    line-height: 1.3;
}

.OrderDetails .order-id-wpr {
    padding: 16px;
    padding-top: 8px;
    border-radius: 24px;
    border: 1px solid var(--primary-color-l-2);
    margin-top: 12px;
}

.OrderDetails .order-id-ctr {
    /* margin-top: 20px; */
    /* padding: 0 20px; */
    display: flex;
    justify-content: space-between;
}

.OrderDetails .order-id-ctr h2 {
    font-size: 36px;
    font-weight: 600;
    color: var(--secondary-color-d-3);
}

.OrderDetails .order-id-ctr button, .OrderDetails .order-id-ctr button:focus {
    color: var(--primary-color-l-2);
    text-decoration: none;
    transition: color 100ms ease-out;
    background-color: transparent;
    outline: 0;
    border: 0;
    cursor: pointer;
}
.OrderDetails .order-id-ctr button:hover {
    color: var(--primary-color);
}
.OrderDetails .order-id-ctr button span {
    margin-right: 8px;
    font-size: 16px;
    display: inline-block;
    /* transform: translateY(-1px); */
}
.OrderDetails .order-id-ctr button * {
    vertical-align: middle;
}

.OrderDetails .order_id_help-text {
    color: var(--primary-color);
    text-align: left;
    margin-top: 0;
    line-height: 1.3;
}

.OrderDetails .status-text {
    color: var(--primary-color);
    line-height: 1.4;
    font-size: 20px;
    margin-top: 12px;
    margin-bottom: 12px;
    /* text-align: left; */
}

.OrderDetails .status-text span {
    color: var(--primary-color-l-2);
    font-size: 16px;
}

.OrderDetails .receipt-text-grp {
    display: flex;
    justify-content: space-between;
}

.OrderDetails .receipt-text {
    display: block;
    font-size: 20px;
    margin: 8px 0;
    color: var(--primary-color);
}

.OrderDetails .text-center {
    text-align: center;
}

.OrderDetails .text-right {
    text-align: right;
}

.OrderDetails .delivery-details-items-ctr .receipt-text-grp {
    display: block;
}
.OrderDetails .delivery-details-items-ctr .receipt-text-grp .receipt-text {
    margin: 0;
}
.OrderDetails .delivery-details-items-ctr .receipt-text-grp .receipt-text:last-child {
    margin-bottom: 8px;
}
.OrderDetails .delivery-details-items-ctr .receipt-text-grp .hint-text {
    font-size: 16px !important;
    color: var(--primary-color-l-2) !important;
    margin-top: 8px;
}