.Orders {
    padding: 0 20px;
    padding-bottom: 116px;
}

.Orders .divider {
    outline: 0;
    border: 0;
    border-top: 1px solid var(--secondary-color-d-3);
    width: calc(100% + 40px);
    margin-left: -20px;
}

.Orders .empty-cart-cta {
    width: auto;
    height: 64px;
    background-color: var(--secondary-color-d-3);
    border: 0;
    outline: 0;
    border-radius: 24px;
    display: inline-flex;
    margin-top: 24px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0 20px;
    transition: all 50ms ease-out;
    font-size: 24px;
    color: white;
    font-weight: normal;
    text-decoration: none;
}
.Orders .empty-cart-cta:hover {
    background-color: var(--secondary-color-d-2);
}
.Orders .empty-cart-cta:active {
    background-color: var(--secondary-color-d-4);
}

.Orders .order-link {
    font-size: 24px;
    padding: 16px;
    border-radius: 24px;
    margin-top: 20px;
    border: 1px solid var(--secondary-color-d-3);
    color: var(--primary-color);
    text-decoration: none;
    width: 100%;
    display: flex;
    justify-content: space-between;
    line-height: 1;
}
.Orders .order-link:active {
    border-width: 2px;
    padding: 15px;
}
.Orders .order-link strong {
    font-weight: 600;
}
/* .Orders .order-link strong.title {
    color: var(--secondary-color-d-3);
} */
.Orders .order-link .small-text {
    font-size: 16px;
    color: var(--primary-color-l-2);
}
.Orders .order-link img {
    transform: rotate(180deg);
}