@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap);
*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root * {
  font-family: 'Source Sans Pro', sans-serif;
  /* -webkit-tap-highlight-color: transparent; */
}

:root {
  --primary-color: #565679;
  --primary-color-d-1: #42425C;
  --primary-color-d-2: #2C2C3F;
  --primary-color-d-3: #171721;
  --primary-color-l-1: #6D6D97;
  --primary-color-l-2: #8B8BAC;
  --primary-color-l-3: #A9A9C2;
  --primary-color-l-4: #C6C6D7;
  --primary-color-l-5: #E4E4EC;
  --primary-color-l-6: #F6F6F9;

  --secondary-color: #F6F4E2;
  --secondary-color-d-1: #EAE6BD;
  --secondary-color-d-2: #DED796;
  --secondary-color-d-3: #D2C86F;
  --secondary-color-d-4: #C6BA48;
  --secondary-color-l-1: #FBFBF3;

  --viewport-width: 411px;
}

body, html {
  margin: 0 auto;
  width: 100%;
}

body {
  min-height: 100vh;
  max-width: 411px;
  max-width: var(--viewport-width);
  /* max-width: 640px; */
  background-color: #FBFBF3;
  background-color: var(--secondary-color-l-1);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@keyframes spinner {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}

.err-text {
  margin-top: 20px;
  font-size: 16px;
  color: #8B8BAC;
  color: var(--primary-color-l-2);
  line-height: 1.681;
  text-align: center;
}

.title-text-ctr {
  font-size: 36px;
  margin-top: 20px;
  color: #8B8BAC;
  color: var(--primary-color-l-2);
  margin-bottom: 20px;
}
.title-text-ctr span {
  color: #565679;
  color: var(--primary-color);
  font-weight: 600;
}

.divider {
  outline: 0;
  border: 0;
  border-top: 1px solid #D2C86F;
  border-top: 1px solid var(--secondary-color-d-3);
  width: calc(100% + 40px);
  margin-left: -20px;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.App {
  background-color: transparent;
}

.Home {
    padding: 0 20px;
    padding-bottom: 116px;
}

.Home .about-tagline {
    font-size: 24px;
    font-weight: normal;
    color: var(--primary-color);
    margin-top: 8px;
}

.Home .how-to-points {
    margin-top: 8px;
    font-size: 16px;
    color: var(--primary-color-l-2);
    line-height: 1.681;
}

.Home .search-bar-ctr {
    height: 64px;
    width: 100%;
    position: relative;
    margin-top: 16px;
}
.Home .search-bar-ctr .search-bar {
    position: absolute;
    display: block;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    border-radius: 24px;
    font-size: 24px;
    color: var(--primary-color);
    border: 1px solid var(--secondary-color-d-3);
    padding: 16px 20px;
    outline: 0;
}
.Home .search-bar-ctr .search-bar::-webkit-input-placeholder {
    color: var(--primary-color-l-2);
}
.Home .search-bar-ctr .search-bar:-ms-input-placeholder {
    color: var(--primary-color-l-2);
}
.Home .search-bar-ctr .search-bar::-ms-input-placeholder {
    color: var(--primary-color-l-2);
}
.Home .search-bar-ctr .search-bar::placeholder {
    color: var(--primary-color-l-2);
}
.Home .search-bar-ctr .search-bar:focus {
    border-width: 2px;
    padding: 15px 19px;
    outline: 0;
}
.Home .search-bar-ctr .search-icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
}
.HomeHeader {
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.HomeHeader .icons-ctr a {
    margin-left: 32px;
    transition: opacity 100ms ease-out;
}
.HomeHeader .icons-ctr a:hover {
    margin-left: 32px;
    opacity: 0.6;
}
.HomeHeader .icons-ctr a:first {
    margin-left: 16px;
}
.ProductCard {
    width: 100%;
    border-radius: 24px;
    background-color: white;
    border: 1px solid var(--secondary-color-d-3);
    margin-top: 20px;
}

.ProductCard .img-ctr {
    display: block;
    text-decoration: none;
    border-radius: 24px;
    width: 100%;
    overflow: hidden;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}
.ProductCard .img-ctr::after {
    content: "";
    display: block;
    padding-bottom: 100%;
}

.ProductCard .details-ctr {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
}

.ProductCard .details {
    display: block;
    width: calc(100% - 64px);
    text-decoration: none;
    /* margin-top: 12px; */
    padding: 16px;
    /* padding-top: 0; */
}

.ProductCard .details .title {
    font-size: 24px;
    font-weight: 600;
    color: var(--primary-color);
}

.ProductCard .details .price-details {
    font-size: 16px;
    /* font-weight: 600; */
    color: var(--primary-color-l-2);
}

.ProductCard .add-to-cart-btn {
    width: 64px;
    height: 64px;
    background-color: var(--secondary-color-d-3);
    border: 0;
    outline: 0;
    border-bottom-right-radius: 24px;
    border-top-left-radius: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 50ms ease-out;
}
.ProductCard .add-to-cart-btn:hover {
    background-color: var(--secondary-color-d-2);
}
.ProductCard .add-to-cart-btn:active {
    background-color: var(--secondary-color-d-4);
}
.BottomNavbar {
    width: 100%;
    max-width: var(--viewport-width);
    height: 68px;
    position: fixed;
    bottom: 0;
    right: auto;
    left: auto;
    top: auto;
    transform: translateX(-20px);
    box-sizing: content-box;
    display: flex;
    border: 1px solid var(--secondary-color-d-3);
    border-top-right-radius: 24px;
    border-top-left-radius: 24px;
    background-color: white;
    justify-content: space-around;
    align-items: flex-end;
}

.BottomNavbar .nav-item-ctr {
    text-align: center;
    text-decoration: none;
    color: var(--primary-color-l-2);
    font-weight: normal;
    margin-bottom: 6px;
}
.BottomNavbar .nav-item-ctr:hover span.nav-link-text {
    color: var(--primary-color);
}
.BottomNavbar .nav-item-ctr span.nav-link-text {
    margin-top: 4px;
    transition: color 100ms ease-out;
}
.BottomNavbar .nav-item-ctr span.nav-link-text.active {
    color: var(--primary-color);
    font-weight: 600;
}
.BottomNavbar .prominent-item .prominent-icon-ctr {
    width: 72px;
    height: 72px;
    display: inline-block;
    border-radius: 10000px;
    border: 3px solid white;
    background-color: white;
    overflow: hidden;
}
.BottomNavbar .prominent-item .prominent-icon-inner-ctr {
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 10000px;
    padding-top: 2px;
    border: 2px solid var(--secondary-color-d-3);
}
.BottomNavbar .prominent-item .prominent-icon-ctr span {
    font-size: 20px;
    font-weight: bold;
    color: var(--secondary-color-d-3);
}
.BottomNavbar .prominent-item .prominent-icon-ctr img {
    margin-top: -4px;
}
