.LoadingSpinner {
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 40px;
    height: 40px;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    border: 2px solid transparent;
    border-top-color: var(--secondary-color-d-3);
    border-bottom-color: var(--secondary-color-d-3);
    animation: spinner 1s ease infinite;
