.BottomSheet {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    max-width: 100vw;
    max-height: 100vh;
    display: flex;
    visibility: hidden;
    align-items: flex-end;
    justify-content: center;
    background-color: transparent;
    /* backdrop-filter: blur(2px); */
    transition: all 200ms ease-out;
}
.BottomSheet.visible {
    visibility: visible;
    background-color: #17172142;
}

.BottomSheet.visible .bg-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.BottomSheet .bottom-sheet-wpr {
    width: 100%;
    height: 100%;
    max-width: var(--viewport-width);
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;
    justify-content: center;
    overflow-y: hidden;
    padding-top: 40vh;
    position: relative;
}
.BottomSheet .bottom-sheet-wpr.scrollable {
    overflow-y: auto;
}

.BottomSheet .bottom-sheet-wpr .close-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.BottomSheet .sheet {
    width: 100%;
    background-color: white;
    border: 1px solid var(--secondary-color-d-3);
    padding: 20px;
    border-top-right-radius: 24px;
    border-top-left-radius: 24px;
    transform: translateY(100%);
    overflow: hidden;
    transition: transform 200ms ease-out;
}
.BottomSheet.visible .sheet {
    transform: translateY(0);
}

.BottomSheet .sheet .sheet-title-ctr {
    display: flex;
    justify-content: space-between;
}

.BottomSheet .sheet .sheet-title-ctr .sheet-title {
    font-size: 24px;
    color: var(--primary-color-l-2);
    padding: 0 8px;
    text-align: center;
}

.BottomSheet .sheet .sheet-title-ctr .close-icon {
    cursor: pointer;
    transition: opacity 100ms ease-out;
}

.BottomSheet .sheet .sheet-title-ctr .close-icon:hover {
    opacity: 0.6;
}

.BottomSheet .sheet .sheet-title-ctr .close-icon:active {
    opacity: 1;
}
.AddToCartSheetContent {
    padding-top: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.AddToCartSheetContent .product-title {
    font-size: 24px;
    color: var(--primary-color);
    text-align: center;
    font-weight: normal;
}

.AddToCartSheetContent .qty-label {
    margin-top: 20px;
    font-size: 16px;
    color: var(--primary-color);
    font-weight: normal;
}

.AddToCartSheetContent .quantity-input-ctr {
    margin-top: 8px;
}

.AddToCartSheetContent .quantity-input-ctr input, .AddToCartSheetContent .quantity-input-ctr button {
    width: 64px;
    height: 64px;
    background-color: white;
    outline: 0;
    border: 1px solid var(--secondary-color-d-3);
    font-size: 24px;
    color: var(--primary-color);
    padding: 16px 0;
    text-align: center;
}
.AddToCartSheetContent .quantity-input-ctr input {
    margin: 0 -1px;
}
.AddToCartSheetContent .quantity-input-ctr input:focus {
    border-width: 2px;
    padding: 15px 0;
}
.AddToCartSheetContent .quantity-input-ctr button {
    cursor: pointer;
    padding: 0;
}
.AddToCartSheetContent .quantity-input-ctr button:active {
    border-width: 2px;
}
.AddToCartSheetContent .quantity-input-ctr button.dec-btn {
    border-top-left-radius: 24px;
    border-bottom-left-radius: 24px;
}
.AddToCartSheetContent .quantity-input-ctr button.inc-btn {
    border-top-right-radius: 24px;
    border-bottom-right-radius: 24px;
}
.AddToCartSheetContent .quantity-input-ctr button img {
    vertical-align: middle;
    margin: 0;
    transform: translateY(-2px);
}

.AddToCartSheetContent .confirm-btn {
    height: 64px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--secondary-color-d-3);
    color: white;
    outline: 0;
    border: 0;
    border-radius: 24px;
    margin-top: 20px;
    padding: 0 20px;
    font-size: 24px;
    font-weight: normal;
    cursor: pointer;
    transition: background-color 150ms ease-out;
}
.AddToCartSheetContent .confirm-btn:hover {
    background-color: var(--secondary-color-d-2);
}
.AddToCartSheetContent .confirm-btn:active {
    background-color: var(--secondary-color-d-4);
}
