.ImagesSlider .react_lightgallery_item {
    display: inline-block;
    overflow: hidden;
    width: 70vw;
    max-width: calc(var(--viewport-width) - (var(--viewport-width) / 100 * 30));
    border-radius: 24px;
    margin-right: 20px;
}
.ImagesSlider .image-ctr {
    display: inline-block;
    overflow: hidden;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 70vw;
    max-width: calc(var(--viewport-width) - (var(--viewport-width) / 100 * 30));
    border-radius: 24px;
    margin-right: 20px;
    border: 1px solid var(--secondary-color-d-3);
    cursor: pointer;
}
.ImagesSlider .image-ctr:last-child {
    margin-right: 0;
}
.ImagesSlider .image-ctr::after {
    content: "";
    display: block;
    padding-bottom: 100%;
}
.TopBar {
    padding-top: 20px;
    /* padding: 0 20px; */
