.ProductCard {
    width: 100%;
    border-radius: 24px;
    background-color: white;
    border: 1px solid var(--secondary-color-d-3);
    margin-top: 20px;
}

.ProductCard .img-ctr {
    display: block;
    text-decoration: none;
    border-radius: 24px;
    width: 100%;
    overflow: hidden;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}
.ProductCard .img-ctr::after {
    content: "";
    display: block;
    padding-bottom: 100%;
}

.ProductCard .details-ctr {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
}

.ProductCard .details {
    display: block;
    width: calc(100% - 64px);
    text-decoration: none;
    /* margin-top: 12px; */
    padding: 16px;
    /* padding-top: 0; */
}

.ProductCard .details .title {
    font-size: 24px;
    font-weight: 600;
    color: var(--primary-color);
}

.ProductCard .details .price-details {
    font-size: 16px;
    /* font-weight: 600; */
    color: var(--primary-color-l-2);
}

.ProductCard .add-to-cart-btn {
    width: 64px;
    height: 64px;
    background-color: var(--secondary-color-d-3);
    border: 0;
    outline: 0;
    border-bottom-right-radius: 24px;
    border-top-left-radius: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 50ms ease-out;
}
.ProductCard .add-to-cart-btn:hover {
    background-color: var(--secondary-color-d-2);
}
.ProductCard .add-to-cart-btn:active {
    background-color: var(--secondary-color-d-4);
}