.ProductDetails {
    padding: 0 20px;
    padding-bottom: 116px;
}

.ProductDetails .title {
    font-size: 36px;
    font-weight: 600;
    color: var(--primary-color);
    margin-top: 10px;
    width: calc(100% - 64px);
    padding-right: 20px;
}

.ProductDetails .price-details {
    font-size: 24px;
    font-weight: normal;
    color: var(--primary-color-l-2);
    margin-top: 4px;
    width: calc(100% - 64px);
    padding-right: 20px;
}

.ProductDetails .description {
    font-size: 16px;
    line-height: 1.681;
    color: var(--primary-color-l-2);
    margin-top: 20px;
    width: calc(100% - 64px);
    padding-right: 20px;
}

.ProductDetails .add-to-cart-btn-ctr {
    width: 100%;
    max-width: var(--viewport-width);
    position: fixed;
    top: auto;
    bottom: 88px;
    right: auto;
    left: auto;
    display: flex;
    justify-content: flex-end;
    padding-right: 40px;
}

.ProductDetails .add-to-cart-btn {
    width: 64px;
    height: 64px;
    background-color: var(--secondary-color-d-3);
    border: 0;
    outline: 0;
    border-bottom-right-radius: 24px;
    border-top-left-radius: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 50ms ease-out;
}
.ProductDetails .add-to-cart-btn:hover {
    background-color: var(--secondary-color-d-2);
}
.ProductDetails .add-to-cart-btn:active {
    background-color: var(--secondary-color-d-4);
}