.BottomSheet {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    max-width: 100vw;
    max-height: 100vh;
    display: flex;
    visibility: hidden;
    align-items: flex-end;
    justify-content: center;
    background-color: transparent;
    /* backdrop-filter: blur(2px); */
    transition: all 200ms ease-out;
}
.BottomSheet.visible {
    visibility: visible;
    background-color: #17172142;
}

.BottomSheet.visible .bg-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.BottomSheet .bottom-sheet-wpr {
    width: 100%;
    height: 100%;
    max-width: var(--viewport-width);
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;
    justify-content: center;
    overflow-y: hidden;
    padding-top: 40vh;
    position: relative;
}
.BottomSheet .bottom-sheet-wpr.scrollable {
    overflow-y: auto;
}

.BottomSheet .bottom-sheet-wpr .close-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.BottomSheet .sheet {
    width: 100%;
    background-color: white;
    border: 1px solid var(--secondary-color-d-3);
    padding: 20px;
    border-top-right-radius: 24px;
    border-top-left-radius: 24px;
    transform: translateY(100%);
    overflow: hidden;
    transition: transform 200ms ease-out;
}
.BottomSheet.visible .sheet {
    transform: translateY(0);
}

.BottomSheet .sheet .sheet-title-ctr {
    display: flex;
    justify-content: space-between;
}

.BottomSheet .sheet .sheet-title-ctr .sheet-title {
    font-size: 24px;
    color: var(--primary-color-l-2);
    padding: 0 8px;
    text-align: center;
}

.BottomSheet .sheet .sheet-title-ctr .close-icon {
    cursor: pointer;
    transition: opacity 100ms ease-out;
}

.BottomSheet .sheet .sheet-title-ctr .close-icon:hover {
    opacity: 0.6;
}

.BottomSheet .sheet .sheet-title-ctr .close-icon:active {
    opacity: 1;
}