@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap');

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root * {
  font-family: 'Source Sans Pro', sans-serif;
  /* -webkit-tap-highlight-color: transparent; */
}

:root {
  --primary-color: #565679;
  --primary-color-d-1: #42425C;
  --primary-color-d-2: #2C2C3F;
  --primary-color-d-3: #171721;
  --primary-color-l-1: #6D6D97;
  --primary-color-l-2: #8B8BAC;
  --primary-color-l-3: #A9A9C2;
  --primary-color-l-4: #C6C6D7;
  --primary-color-l-5: #E4E4EC;
  --primary-color-l-6: #F6F6F9;

  --secondary-color: #F6F4E2;
  --secondary-color-d-1: #EAE6BD;
  --secondary-color-d-2: #DED796;
  --secondary-color-d-3: #D2C86F;
  --secondary-color-d-4: #C6BA48;
  --secondary-color-l-1: #FBFBF3;

  --viewport-width: 411px;
}

body, html {
  margin: 0 auto;
  width: 100%;
}

body {
  min-height: 100vh;
  max-width: var(--viewport-width);
  /* max-width: 640px; */
  background-color: var(--secondary-color-l-1);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@keyframes spinner {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}

.err-text {
  margin-top: 20px;
  font-size: 16px;
  color: var(--primary-color-l-2);
  line-height: 1.681;
  text-align: center;
}

.title-text-ctr {
  font-size: 36px;
  margin-top: 20px;
  color: var(--primary-color-l-2);
  margin-bottom: 20px;
}
.title-text-ctr span {
  color: var(--primary-color);
  font-weight: 600;
}

.divider {
  outline: 0;
  border: 0;
  border-top: 1px solid var(--secondary-color-d-3);
  width: calc(100% + 40px);
  margin-left: -20px;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}
