.Home {
    padding: 0 20px;
    padding-bottom: 116px;
}

.Home .about-tagline {
    font-size: 24px;
    font-weight: normal;
    color: var(--primary-color);
    margin-top: 8px;
}

.Home .how-to-points {
    margin-top: 8px;
    font-size: 16px;
    color: var(--primary-color-l-2);
    line-height: 1.681;
}

.Home .search-bar-ctr {
    height: 64px;
    width: 100%;
    position: relative;
    margin-top: 16px;
}
.Home .search-bar-ctr .search-bar {
    position: absolute;
    display: block;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    border-radius: 24px;
    font-size: 24px;
    color: var(--primary-color);
    border: 1px solid var(--secondary-color-d-3);
    padding: 16px 20px;
    outline: 0;
}
.Home .search-bar-ctr .search-bar::placeholder {
    color: var(--primary-color-l-2);
}
.Home .search-bar-ctr .search-bar:focus {
    border-width: 2px;
    padding: 15px 19px;
    outline: 0;
}
.Home .search-bar-ctr .search-icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
}